/*
	BOOTSTRAP OVERRIDE RULES
	Please do not add the .fuelux namespace wrapper class.
 */

.popover {
	background: @popover-bg;
	border: 1px solid @popover-border-color;
	padding: 0;

	&.bottom > .arrow {
		border-bottom-color: @popover-border-color;
		// border-image: linear-gradient(to bottom, @popover-border-color, @popover-title-bg) 1 100%;

		&:after {
			// display: none;
			border-bottom-color: @popover-title-bg;
		}
	}

	&.left > .arrow {
		border-left-color: @popover-border-color;

		&:after {
			border-left-color: @popover-bg;
		}
	}

	&.right > .arrow {
		border-right-color: @popover-border-color;

		&:after {
			border-right-color: @popover-bg;
		}
	}

	&.top > .arrow {
		border-top-color: @popover-border-color;

		&:after {
			border-top-color: @popover-bg;
		}
	}

	&-content {
		font-size: 12px;
		padding: 10px;
	}

	&-title {
		background-color: @popover-title-bg;
		border-bottom: 1px solid @popover-border-color;
		border-radius: (@border-radius-large - 1) (@border-radius-large - 1) 0 0;

		color: @gray33;
		font-size: 12px;
		font-weight: bold;
		line-height: 14px;
		// margin: -1px -1px 0;
		padding: 4px 8px;
	}
}