/*
	BOOTSTRAP OVERRIDE RULES
	Please do not add the .fuelux namespace wrapper class.
 */

//
// Buttons
// --------------------------------------------------


// Base styles
// --------------------------------------------------
@import 'mixins.less';

.btn {
	.btn();
}


// Alternate buttons
// --------------------------------------------------

// .btn-secondary is only present for compatibility with bootstrap and MCtheme 
// prior to v1.8.0. Please do not use .btn-secondary
.btn-default, .btn-secondary {
	.btn-default();
}

.btn-basic {
	.btn-basic();
}

.btn-primary {
	.btn-primary();
}

// Success appears as green
.btn-success {
	//#gradient > .gradient-bg(lighten(@btn-success-bg, 15%), @btn-success-bg);
	.button-variant(@btn-success-color;
									lighten(@btn-success-bg, 18%); @btn-success-bg;
									lighten(@btn-success-bg, 20%); lighten(@btn-success-bg, 5%);
									darken(@btn-success-bg, 10%); darken(@btn-success-bg, 2%);
									lighten(@btn-success-bg, 30%); lighten(@btn-success-bg, 40%));

	&.dropdown-toggle .caret {
		color: @true-white;
	}

}
// Info appears as blue-green
//.btn-info {
	//.button-variant(@btn-info-color; @btn-info-bg; @btn-info-border);
//}
//// Warning appears as orange
.btn-warning {
	.button-variant(@btn-warning-color;
									lighten(@btn-warning-bg, 18%); @btn-warning-bg;
									lighten(@btn-warning-bg, 20%); lighten(@btn-warning-bg, 5%);
									darken(@btn-warning-bg, 10%); darken(@btn-warning-bg, 2%);
									lighten(@btn-warning-bg, 30%); lighten(@btn-warning-bg, 40%));

	&.dropdown-toggle .caret {
		color: @true-white;
	}
}
// Danger and error appear as red
.btn-danger {
	.button-variant(@btn-danger-color;
									@brand-danger-medium; @brand-danger-dark;
									lighten(@brand-danger-medium, 5%); lighten(@brand-danger-dark, 5%);
									darken(@brand-danger-medium, 10%); darken(@brand-danger-dark, 2%);
									lighten(@brand-danger-medium, 30%); lighten(@brand-danger-dark, 40%));

	&.dropdown-toggle .caret {
		color: @true-white;
	}

}

// Link buttons
// -------------------------

// Make a button look and behave like a link
.btn-link {
	.btn-link();
}


// Button Sizes
// --------------------------------------------------

.btn-lg, .btn-group-lg .btn-lg {
	.button-size( 7px; 10px; @font-size-small; @border-radius-large);
}

.btn-sm, .btn-group-sm .btn-sm,
.btn-xs, .btn-group-xs .btn-xs {
	.button-size( 3px; 8px; @font-size-x-small; @border-radius-small);
	line-height: 10px;
}


// Block button
// --------------------------------------------------

.btn-block {
	display: block;
	width: 100%;
}

// Vertically space out multiple block buttons
.btn-block + .btn-block {
	margin-top: 5px;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
	&.btn-block {
		width: 100%;
	}
}

// Checkmark Toggle Button Groups
// --------------------------------------------------

.btn-group-check-toggle {
	span {
		font-size: 0;
	}

	label.active {
		background: none;
		background-color: @brand-primary-medium; // light blue
		color: @true-white;

		span {
			font-size: inherit;
			line-height: 8px;
		}

	}

}

.btn-group-switch {
	label.active:first-child {
		background: none;
		background-color: @brand-success-medium;
	}

	label.active:last-child {
		color: @true-white;
		background: none;
		background-color: @brand-danger-medium;
	}

}
