.fuelux .repeater {


	&-filters {
		// margin-top: 2px;
		margin-top: 0px;
	}

	&-header {
		background: @gray96;
		box-shadow: 0px 0px 0px 1px @true-white inset;
		padding: 10px 13px 10px 10px;

		.control.change-columns {

			button.btn.btn-default.fuelux-icon-button.dropdown-toggle {
				padding-right: 4px;
				padding-left: 7px;
			}
		}
		
	}

	&-footer {
		background: @gray96;
		box-shadow: 0px 0px 0px 1px @true-white inset;
		.footer-with-inputs();
		min-height: 0;
	}

	&-itemization {
		font-size: 12px;
		margin-top: 3px;

		.selectlist {
			margin: 0 4px;
			position: relative;
			top: -1px;
		}
	}

	&-pagination {
		font-size: 12px;
		margin-top: 2px;

		.repeater-primaryPaging {
			width: 52px;
		}


		.repeater-next, .repeater-prev {
			.btn-default;
			border: 1px solid @gray87;

			height: 17px;
			position: relative;
			top: -1px;
			width: 17px;

			.glyphicon {
				display: block;
				left: 6px;
				position: absolute;
				top: 4px;
				width: 8px;
				height: 8px;
				background-image: none;

				&:before {
					display: none;
				}
			}
		}

		.repeater-next {
			.glyphicon {
				border-color: transparent transparent transparent @brand-primary-medium;
				border-style: solid none solid solid;
				border-width: 4px medium 4px 4px;
			}
		}

		.repeater-prev {
			.glyphicon {
				border-color: transparent @brand-primary-medium transparent transparent;
				border-style: solid solid solid none;
				border-width: 4px 4px 4px medium;
				left: 2px;
			}
		}

		.repeater-primaryPaging {
			background: @true-white;
			border-radius: 4px;
		}

		.repeater-secondaryPaging {
			width: 37px;
		}
	}

	&-search {
		margin-top: 2px;

		.input-group {
			.form-control {
				border-radius: 4px;

				&:first-child {
					border-radius: 4px;
				}
			}
		}
	}

	&-title {
		font-size: 14px;
		line-height: 25px
	}

	&-views {
		// margin-top: 2px;
		margin-top: 0px;

		.btn-secondary {
			background-repeat: no-repeat;
			font-size: 14px;
			padding: 2px 4px 3px;
		}
	}
}