.fuelux {

	// Font Family
	// -------------------------
	.serif {
		font-family: Georgia, "Times New Roman", Times, serif;
	}
	.monospace {
		font-family: Menlo, Monaco, Consolas, "Courier New", monospace;
	}

	.title-form, .title-header-page, .title-table {
		line-height: @line-height-base;

		small,
		.small {
			font-weight: normal;
			line-height: 1;
			color: @gray47;
			font-size: 65%;
		}
	}

	.title-table { font-size: @font-size-h2; }

	.title-form { font-size: @font-size-h3; }

	// Page header
	// -------------------------
	.page-header {
		font-size: @font-size-h1;
		margin-top: 10px;
		border-bottom: none;
		margin-bottom: 0;

		h1 {
			line-height: 30px;	// matches create button height
			margin-top: 0;
			margin-bottom: 0;
		}

		// create button, etc.
		& > :last-of-type:not(:only-of-type) {
			text-align: right
		}

	}

	// Font Size
	// -------------------------
	.text-sm {
		font-size: 10px;
	}
	.text-default {
		font-size: 11px;
	}
	.text-lg {
		font-size: 24px;
	}
	.text-xl {
		font-size: 32px;
	}
	.text-xxl {
		font-size: 44px;
	}

	// Padding
	// -------------------------
	.padding-xs {
		padding: 5px;
	}
	.padding-xs-horizontal {
		padding: 0 5px;
	}
	.padding-xs-vertical {
		padding: 5px 0;
	}
	.padding-xs-top {
		padding-top: 5px;
	}
	.padding-xs-bottom {
		padding-bottom: 5px;
	}
	.padding-xs-left {
		padding-left: 5px;
	}
	.padding-xs-right {
		padding-right: 5px;
	}
	.padding-sm {
		padding: 10px;
	}
