	}
	.padding-sm-bottom {
		padding-bottom: 10px;
	}
	.padding-sm-left {
		padding-left: 10px;
	}
	.padding-sm-right {
		padding-right: 10px;
	}
	.padding-md {
		padding: 15px;
	}
	.padding-md-horizontal {
