	// Helpers
	// -------------------------
	.data-label {
		text-transform: uppercase;
		font-size: 10px;
		margin-bottom: 2px;
		text-shadow: 0 1px #fff;
		color: #666;
	}
	.data-value {
		font-size: 14px;
	}
	.section-heading {
		font-size: 16px;
		color: #333;
		margin: 0 0 10px;
		padding: 0;
		text-shadow: 0 1px #fff;
	}

}